<template>
	<div class="page-content-server">
		<CompanyBar />
		<div class="company-bar"></div>

		<div class="content-info">
			<!-- <div class="tabs-striped">
				<span class="tabs-item" :class="{'tabs-item-active': tabIndex == index}" v-for="(item, index) in tabs" @click="onTabClick(index)">{{item}}</span>
			</div> -->

			<div class="tabs-striped-content">
				<scroller class="scroller"
					:on-infinite="onInfinite"
					ref="scroller">
					<CompanyHeader :companySwiper="companySwiper" />
					<div style="height: 8px; background: #eee;"></div>

					<div class="tabs-striped-content-items">
						<router-link tag="div" class="tabs-striped-content-item" :to="{'path': '/detail', query: { id: item.id }}" v-for="item in items">
							<img :src="item.headimg">

							<div class="img-info">
								<h3>{{item.name}}</h3>
								<div class="item-info">{{item.content}}</div>
								<div class="time">{{item.dateTime}}</div>
							</div>
						</router-link>
					</div>
				</scroller>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import _ from 'lodash'
	import CompanyBar from './component/company-bar.vue'
	import CompanyHeader from './component/company-header.vue'

	export default {
		props: ['from'],
		components: { CompanyBar, CompanyHeader },
		data() {
			return {
				companySwiper: {
					titleName: '服务案例',
					titleInfo: '权威、专业、全面的医疗咨询服务',
					className: 'server'
				},
				tabs: ["精密体验", "血液净化", "干细胞抗衰"],
				tabIndex: 0,
				items: this.$experience.data.list,
				scrollNext: true,
			}
		},
		mounted() {
			// if (this.tabIndex == 0) fnTiyan(this);

			// switch(this.from) {
			// 	case 'Experience':
			// 		this.tabIndex = 0;
			// 		fnTiyan(this);
			// 		break;
			// 	case 'Purify':
			// 		this.tabIndex = 1;
			// 		fnJinghua(this);
			// 		break;
			// 	case 'Antifading':
			// 		this.tabIndex = 2;
			// 		fnKangshuai(this);
			// 		break;
			// }
		},
		methods: {
			onTabClick(index) {
				this.tabIndex = index;

				switch(index) {
					case 0:
						fnTiyan(this);
						break;
					case 1:
						fnJinghua(this);
						break;
					case 2:
						fnKangshuai(this);
						break;
				}
			},

			onInfinite() {
				// 到底部时加载新数据, 需要根据当前 tabIndex
				// if (this.scrollNext) {
				// 	fnTiyan(this, {});
				// }else{
				// 	if (this.$refs.scroller) this.$refs.scroller.finishInfinite();
				// }

				if (this.$refs.scroller) this.$refs.scroller.finishInfinite();

				// switch(this.tabIndex) {
				// 	case 0:
				// 		fnTiyan(this);
				// 		break;
				// 	case 1:
				// 		fnJinghua(this);
				// 		break;
				// 	case 2:
				// 		fnKangshuai(this);
				// 		break;
				// }
			}
		}
	}

	function fnTiyan(that, params) {
		axios.get(
			that.$api.tiyan + qs.stringify(params),
			{
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				timeout: 300000
			}
		).then(response => {
			let result = response.data;

			if (result.code === 0) {
				if (that.items.length == 0) {
					that.items = result.data.list;
				}else{
					if (that.scrollNext) {
						that.items = _.concat(that.items, result.data.list);
					}
				}
				that.scrollNext = result.data.next;

				setTimeout(() => {
					if (that.$refs.scroller) { that.$refs.scroller.finishInfinite(); }
				})
			}
		}).catch(error => {
			if (error.response && error.response.data && error.response.data.code) {
				alert(error.response.data.msg)
			}
		});
	}

	function fnJinghua(that, params) {
		let response = data1;
		if (response.code === 0) {
			that.items = response.data.list;
		}
	}

	function fnKangshuai(that, params) {
		let response = data2;
		if (response.code === 0) {
			that.items = response.data.list;
		}
	}
</script>

<style lang='scss' scoped>
	.page-content-server {
		height: 100%;

		.company-bar {
			height: 40px;
			overflow: hidden;
		}

		.content-info {
			height: calc(100% - 40px);
			background: #fff;
			overflow: hidden;

			.tabs-striped {
				overflow: hidden;
				background: #fff;
				box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);

				span {
					display: inline-block;
					float: left;
					width: calc(100%/3);
					line-height: 40px;
					text-align: center;
					position: relative;

					&.tabs-item-active:after {
						content: "";
						display: block;
						position: absolute;
						left: calc(50% - 14px);
						bottom: 0;
						width: 28px;
						height: 2px;
						border-radius: 10px;
						background: #00A9D6;
						transition: all .3s;
					}
				}
			}

			.tabs-striped-content {
				height: 100%;
				padding: 0 16px;
				overflow: hidden;
				position: relative;

				.scroller {
					height: 100%;

					&>div {
						height: 100%;
					}
				}

				.tabs-striped-content-items {
					background: #fff;
					padding: 0 16px;
				}

				.tabs-striped-content-item {
					height: 120px;
					padding: 20px 0;
					border-top: .5px solid #eee;
					overflow: hidden;

					img {
						width: 120px;
						float: left;
					}

					h3 {
						color: #333;
						font-size: 16px;
						margin-bottom: 7px;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
						font-weight: 700;
					}

					.img-info {
						color: #999;
						width: calc(100% - 122px);
						float: left;
						padding-left: 12px;

						.item-info {
							font-size: 13px;
							margin-bottom: 0;
							display: -webkit-box;
							-webkit-box-orient: vertical;
							-webkit-line-clamp: 2;
							overflow: hidden;
							/* autoprefixer: off */
							-webkit-box-orient: vertical;
							/* autoprefixer: on */
						}

						.time {
							font-size: 12px;
							float: left;
							margin-bottom: 0;
						}
					}
				}

				div.tabs-striped-content-item:first-child {
					border-top: none;
				}
			}

			.tabs-striped-content-header {
				height: calc(100% - 48px);
			}
		}
	}
</style>


