<template>
	<div>
		<div class="menu-mask" :class="{'no': show}"></div>
		<div class="page-content-menu" :class="{'page-content-menu-open': show}" @click="closeMenu()">
			<div class="menu">
				<div class="admin-aside-nemu-logo">
					<img src="../img/logo/logo_menu.png">
				</div>

				<div class="menu-wapper">
					<ul class="list-taipu">
						<li @click="changeTabbar('Home', '首页')">首页</li>
					</ul>

					<ul class="list-taipu">
						<li @click="changeTabbar('Experience', '精密体验')">精密体验</li>
						<li @click="changeTabbar('Purify', '血液净化')">血液净化</li>
						<li @click="changeTabbar('Antifading', '干细胞抗衰')">干细胞抗衰</li>
					</ul>

					<ul class="list-taipu">
						<li @click="changeTabbar('Hospital', '合作医院')">合作医院</li>
						<li @click="changeTabbar('Item', '专家团队')">专家团队</li>
						<li @click="changeTabbar('Server', '服务案例')">服务案例</li>
						<li @click="changeTabbar('About', '关于泰璞健康')">关于泰璞健康</li>
						<li @click="changeTabbar('Consult', '联系我们')">联系我们</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: [ 'show' ],
		data() {
			return {
				returnRes: {}
			}
		},
		mounted() {
			//
		},
		methods: {
			closeMenu() {
				this.returnRes.activeType = sessionStorage.activeType;
				this.returnRes.show = false;
				this.$emit('returnMenuRes', this.returnRes);
			},
			changeTabbar(type, title) {
				this.returnRes.activeType = type;
				this.returnRes.title = title;
				this.returnRes.from = 'Menu';
				this.$emit('returnMenuRes', this.returnRes);
			}
		}
	}
</script>

<style lang='scss' scoped>
	.menu-mask.no{
		display: block;
		animation: fadeIn 0.3s;
	}
	.menu-mask{
		width: 100%;
		height: 100vh;
		position: absolute;
		z-index: 999;
		top: 0;
		left: 0;
		background: rgba(0,0,0,.4);
		display: none;
	}
	@keyframes fadeIn{
		0% {
			opacity: 0;
		}
		100%{
			opacity: 1;
		}
	}
	.page-content-menu {
		position: absolute;
		z-index: 1000;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		// background: rgba(0,0,0,.5);
		transform: translate3d(-100%,0,0);
		transition: all .3s;

		.menu {
			width: 240px;

			.admin-aside-nemu-logo {
				height: 60px;
				background: #00A9D6;
				overflow: hidden;

				img {
					width: 82px;
					height: 20px;
					margin-top: 20px;
					margin-left: 20px;
				}
			}

			.menu-wapper {
				padding: 20px;
			}

			ul.list-taipu {
				padding: 10px 0;
				border-bottom: 1px solid #eee;

				li {
					line-height: 22px;
					font-size: 16px;
					color: #333;
					padding: 10px 0;
				}

				&:last-child {
					border-bottom: none;
				}
			}
		}

	}

	.page-content-menu-open {
		transform: translate3d(0,0,0);
		transition: all .3s;
	}
</style>



