<template>
	<div class="page-content-Hospital">
		<CompanyBar />
		<div class="company-bar"></div>

		<div class="content-info">
			<div class="tabs-striped-content">
				<scroller class="scroller"
					:on-infinite="onInfinite"
					ref="scroller">
					<CompanyHeader :companySwiper="companySwiper" />

					<div class="tabs-striped-content-items">
						<router-link tag="div" class="tabs-striped-content-item" :to="{'path': '/detail-hospital', query: { id: item.id }}" v-for="item in items">
							<img :src="item.headimg">

							<div class="img-info">
								<h2>{{item.name}}</h2>
								<div class="item-info">{{item.content[0]}}</div>
							</div>
						</router-link>
					</div>
				</scroller>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import CompanyBar from './component/company-bar.vue'
	import CompanyHeader from './component/company-header.vue'

	export default {
		components: { CompanyBar, CompanyHeader },
		data() {
			return {
				companySwiper: {
					titleName: '合作医院',
					titleInfo: '日本尖端医疗机构',
					className: 'hospital'
				},
				items: this.$hospital.data.list,
				scrollNext: true,
			}
		},
		mounted() {
			
		},
		methods: {
			onInfinite() {
				// 到底部时加载新数据, 需要根据当前 tabIndex
				// if (this.scrollNext) {
				// 	fnHospital(this, {});
				// }else{
				// 	if (this.$refs.scroller) this.$refs.scroller.finishInfinite();
				// }
				if (this.$refs.scroller) this.$refs.scroller.finishInfinite();
			}
		}
	}

	function fnHospital(that, params) {
		axios.get(
			that.$api.hospital + qs.stringify(params),
			{
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				timeout: 300000
			}
		).then(response => {
			let result = response.data;

			if (result.code === 0) {
				if (that.items.length == 0) {
					that.items = result.data.list;
				}else{
					if (that.scrollNext) {
						that.items = _.concat(that.items, result.data.list);
					}
				}
				that.scrollNext = result.data.next;

				sessionStorage.hospital = JSON.stringify(that.items);

				setTimeout(() => {
					if (that.$refs.scroller) that.$refs.scroller.finishInfinite();
				})
			}
		}).catch(error => {
			if (error.response && error.response.data && error.response.data.code) {
				alert(error.response.data.msg)
			}
		});
	}
</script>

<style lang='scss' scoped>
	.page-content-Hospital {
		height: 100%;

		.company-bar {
			height: 40px;
			overflow: hidden;
		}

		.content-info {
			height: calc(100% - 40px);
			background: #fff;
			overflow: hidden;

			.tabs-striped-content {
				height: 100%;
				overflow: hidden;
				position: relative;

				.scroller {
					height: 100%;

					&>div {
						height: 100%;
					}
				}

				.tabs-striped-content-items {
					background: #fff;
					padding: 12px;
				}

				.tabs-striped-content-item {
					height: 94px;
					border: 1px solid #f2f2f2;
					overflow-y: hidden;
					padding: 12px;
					margin-bottom: 12px;
					box-shadow: 0 2px 2px rgba(0,0,0,.04);

					img {
						width: 120px;
						height: 70px;
						float: left;
					}

					h2 {
						color: #333;
						font-size: 16px;
						line-height: 22px;
						margin-bottom: 2px;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}

					.img-info {
						color: #999;
						width: calc(100% - 120px);
						float: left;
						padding-left: 10px;

						.item-info {
							font-size: 13px;
							line-height: 18px;
							margin-bottom: 3px;
							display: -webkit-box;
							-webkit-box-orient: vertical;
							-webkit-line-clamp: 2;
							overflow: hidden;
							/* autoprefixer: off */
							-webkit-box-orient: vertical;
							/* autoprefixer: on */
						}

						span {
							font-size: 12px;
						}
					}
				}
			}
		}
	}
</style>


