import Vue from 'vue'
import Vonic from 'vonic'
import routes from './router.js'
import Api from './api'
import Experience from '../static/data-mock/experience.json'
import Hospital from '../static/data-mock/hospital.json'

Vue.prototype.$api = Api
Vue.prototype.$experience = Experience
Vue.prototype.$hospital = Hospital

Vue.use(Vonic.app, {
  routes,
})



